import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders, HttpErrorResponse } from '@angular/common/http';
import { Observable, throwError } from 'rxjs';
import { retry, catchError } from 'rxjs/operators';

import { environment } from '../environments/environment';

@Injectable({
  providedIn: 'root'
})
export class BrewskiService {

  public apiPath = environment.apiPath;
  public apiGatewayPath = environment.apiGatewayPath;  
  public apiKey = environment.apiKey;

  httpOptions = {
    headers: new HttpHeaders({
      "Content-Type":  "application/json",
      "X-Api-Key": this.apiKey
    })
  };

  constructor(private http: HttpClient) { }

  // Register
  registerUser(user: any): Observable<any> {
    return this.http
      .post<any>(this.apiGatewayPath + "/register", user, this.httpOptions)
      .pipe(
        retry(2),
        catchError(this.handleError)
      )
  }

  // Get all Brewski
  getAllBrewskis(): Observable<any> {
    return this.http
      .get<any>(this.apiPath, this.httpOptions)
      .pipe(
        retry(2),
        catchError(this.handleError)
      )
  }


  // Handle API errors
  handleError(error: HttpErrorResponse) {
    if (error.error instanceof ErrorEvent) {
      // A client-side or network error occurred. Handle it accordingly.
      console.error('An error occurred:', error.error.message);
    } else {
      // The backend returned an unsuccessful response code.
      // The response body may contain clues as to what went wrong,
      console.error(
        `Backend returned code ${error.status}, ` +
        `body was: ${error.error}`);
    }
    // return an observable with a user-facing error message
    return throwError(
      'Something bad happened; please try again later.');
  };

}
