import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { BrewskiService } from '../brewski.service';

import { from } from 'rxjs';

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css']
})
export class RegisterComponent implements OnInit {

  registerForm: FormGroup;
  submitted = false;
  passwordError= false;

  constructor(private formBuilder: FormBuilder, private brewskiService: BrewskiService) { }

  ngOnInit() {
    this.registerForm = this.formBuilder.group({
      name: ['', Validators.required],
      email: ['', Validators.required],
      password: ['', Validators.required],
      repeatpassword: ['', Validators.required]
    });
  }

  // ngOnInit() {
  //   this.registerForm = this.formBuilder.group({
  //     name: ['', Validators.required],
  //     email: ['', [Validators.required, Validators.email]],
  //     password: ['', [Validators.required, Validators.minLength(4)]],
  //     repeatpassword: ['', [Validators.required, Validators.minLength(4)]]
  //   });
  // }

  // convenience getter for easy access to form fields
  get f() { return this.registerForm.controls; }

  onSubmit() {
    
    this.submitted = true;

    if(this.registerForm.value.password ===  this.registerForm.value.repeatpassword){
      // send form further
      console.log(this.registerForm.value);

      this.brewskiService.registerUser(this.registerForm.value).subscribe((response) => {
        console.log("response==>", response);
      });

      // this.brewskiService.getAllBrewskis().subscribe((response) => {
      //   console.log(response);
      // });


    } else {
      this.passwordError = true;
      this.registerForm.reset();
      
      return;
    }
    
  }

}
